body {
  text-align: center;
  color: #333;
  background: url("pexels-goumbik-616401.59007e8f.jpg") center / cover no-repeat fixed;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  line-height: 1.6;
}

h1 {
  color: #fff;
  background-color: #f7a399;
  padding: 15px;
  font-size: 28px;
  font-weight: bold;
}

header {
  color: #fff;
  text-transform: uppercase;
  background-color: #f7a399;
  padding: 10px 0;
  font-size: 22px;
  font-weight: bold;
  box-shadow: 0 4px 8px #0000001a;
}

main {
  background-color: #fff;
  border-radius: 10px;
  max-width: 600px;
  margin: 20px auto;
  padding: 20px;
  box-shadow: 0 4px 12px #0000001a;
}

input[type="text"] {
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 100%;
  margin: 10px 0;
  padding: 12px;
  font-size: 14px;
}

input:focus {
  outline: 3px solid orange;
  transition: outline .2s;
  box-shadow: 0 0 5px #ffa50099;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #fdd9a0;
  border: none;
  border-radius: 5px;
  padding: 10px 15px;
  font-size: 14px;
  transition: background-color .3s, transform .2s;
}

button:hover {
  background-color: #fbc58a;
  transform: translateY(-2px);
}

button:focus {
  outline: 3px solid orange;
  box-shadow: 0 0 5px #ffa50099;
}

#signIn {
  color: #fff;
  background-color: #6bbf59;
  border-radius: 5px;
  padding: 12px 20px;
  font-size: 16px;
}

#signIn:hover {
  background-color: #5aa64b;
}

#signOutBttn {
  color: #fff;
  background-color: #f28b82;
  border-radius: 5px;
  padding: 10px 20px;
}

#signOutBttn:hover {
  background-color: #e57368;
}

ul {
  margin: 20px 0;
  padding: 0;
  list-style: none;
}

li {
  text-align: left;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 5px;
  justify-content: space-between;
  align-items: center;
  margin: 10px 0;
  padding: 12px;
  transition: background-color .2s;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.recipe-text {
  text-align: left;
  flex: 1;
}

.recipe-buttons {
  gap: 8px;
  display: flex;
}

.delete-btn {
  color: #721c24;
  background-color: #ffe5e5;
}

.delete-btn:hover {
  background-color: #ffc1c1;
}

.edit-btn {
  color: #0c5460;
  background-color: #d5ecff;
}

.edit-btn:hover {
  background-color: #b3dcff;
}

.favorite-btn {
  color: #856404;
  background-color: #fff8c1;
}

.favorite-btn:hover {
  background-color: #ffef99;
}

#chatbot-container {
  z-index: 1000;
  background: #fff8e1;
  border: 1px solid #ccc;
  border-radius: 8px;
  width: 320px;
  padding: 12px;
  transition: max-height .3s ease-in-out;
  position: fixed;
  bottom: 20px;
  right: 20px;
  overflow: hidden;
  box-shadow: 0 4px 6px #0000001a;
}

#chatbot-header {
  color: #fff;
  cursor: pointer;
  background: #f7a399;
  border-radius: 6px 6px 0 0;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  font-size: 16px;
  display: flex;
}

#toggle-chatbot {
  color: #fff;
  cursor: pointer;
  background: none;
  border: none;
  font-size: 20px;
  position: absolute;
  top: 5px;
  right: 5px;
}

#toggle-chatbot:hover {
  color: #fff;
}

#chat-window {
  flex-direction: column;
  max-height: 250px;
  padding: 10px;
  transition: max-height .3s ease-in-out;
  display: flex;
  overflow-y: auto;
}

.chat-hidden #chat-window {
  max-height: 0;
  padding: 0;
  overflow: hidden;
}

#chat-history {
  color: #333;
  background: #fdf6e3;
  border-bottom: 1px solid #ccc;
  border-radius: 5px;
  height: 200px;
  margin-bottom: 10px;
  padding: 8px;
  font-size: 14px;
  overflow-y: auto;
}

.history {
  color: #333;
  background-color: #e7e7e7;
  border-radius: 5px;
  margin-bottom: 8px;
  padding: 8px;
  font-size: 13px;
}

.history.user {
  text-align: left;
  background-color: #e0f7fa;
  font-weight: bold;
}

.history.ai {
  text-align: left;
  background-color: #e8f5e9;
  font-weight: bold;
}

#chat-input {
  border: 1px solid #ccc;
  border-radius: 5px;
  width: calc(100% - 80px);
  padding: 8px;
  font-size: 14px;
}

#send-btn {
  color: #fff;
  background-color: #fdd9a0;
  border-radius: 5px;
  padding: 8px 15px;
  font-size: 14px;
}

#send-btn:hover {
  background-color: #fbc58a;
}

@media (width <= 768px) {
  main {
    width: 100%;
    padding: 15px;
  }

  #chatbot-container {
    width: 90%;
    bottom: 10px;
    right: 10px;
  }
}

@media (width <= 480px) {
  h1 {
    font-size: 22px;
  }

  #chatbot-container {
    width: 95%;
    bottom: 5px;
    right: 5px;
  }
}
